import clipboard from 'clipboard-polyfill'
import { message } from 'antd'

export default url => {
  return clipboard.writeText(url)
    .then(
      val => { message.success(`Copied: ${url.replace(/^(.{20}).*(.{20})$/g, '$1...$2')}`, .7) },
      err => { message.error('Copy url failed, must click on the picture manually') }
    )
}



