import PictureList from '../components/PictureList'
import storage from '../storage/pictures'
import PropTypes from 'prop-types'
import ReactDOM from 'react-dom'
import '../styles/records.scss'
import React from 'react'

export default class Records extends React.Component {
  state = {
    pictures: storage.pictures
  }

  onRemovePicture = token => {
    const pictures = storage.remove(token)
    this.setState({ pictures })
  }

  render () {
    const { onRemovePicture, state: { pictures } } = this

    return (
      <section className="records">
        {pictures.length
          ? <PictureList
              onRemovePicture={onRemovePicture}
              pictures={pictures}
              remove
              grid>
            </PictureList>
          : <p className="empty">Emmmm, Here is the barrens.</p>}
      </section>
    )
  }
}



