import { BrowserRouter as Router, Route, Switch, Link, withRouter } from 'react-router-dom'
import { Scrollbars } from 'react-custom-scrollbars'
import { Menu, Icon, Button } from 'antd'
import Records from './pages/Records'
import Setting from './pages/Setting'
import './styles/theme/dark.scss'
import ReactDOM from 'react-dom'
import Vov from './pages/Vov'
import React from 'react'
import './app.scss'

const AppMenu = withRouter(({ onChange, location, theme, toggleTheme }) => {
  const selectedKeys = [location.pathname.match(/\/[^/]*/g)[0]]
  return (
    <header>
      <h1 className="logo">
        <Link to="/" title="Avi">
          Avi
        </Link>
      </h1>
      
      <section>
        <Menu selectedKeys={selectedKeys} mode="horizontal">
          <Menu.Item key="/">
            <Link to="/">
              <Icon type="home" /> Home
            </Link>
          </Menu.Item>
          <Menu.Item key="/records">
            <Link to="/records">
              <Icon type="picture" /> Records
            </Link>
          </Menu.Item>
          <Menu.Item key="/setting">
            <Link to="/setting">
              <Icon type="setting" /> Setting
            </Link>
          </Menu.Item>
        </Menu>

        <Button
          onClick={() => { toggleTheme(theme) }}
          className="theme-button"
          size="small"
          ghost>
            {theme} Theme
        </Button>
      </section>

    </header>
  )
})

export default class App extends React.Component {
  state = {
    theme: 'Dark',
    themes: ['Dark', 'Light']
  }
  toggleTheme = theme => {
    const { themes } = this.state
    const index = themes.findIndex(v => v === theme) + 1
    this.setState({ theme: themes[index >= themes.length ? 0 : index] })
  }
  render () {
    const { theme } = this.state
    return (
      <Router>
        <section className={`app ${theme.toLowerCase()}`}>
          <AppMenu theme={theme} toggleTheme={this.toggleTheme}></AppMenu>

          <main>
            <Scrollbars>
              <Switch>
                <Route exact path='/' component={Vov}></Route>
                <Route path='/records' component={Records}></Route>
                <Route path='/setting' component={Setting}></Route>
              </Switch>
            </Scrollbars>
          </main>
        </section>
      </Router>
    )
  }
}



